.topSection {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: left;
          align-items: left;
  width: 100%;
}
.topSection .logo {
  position: absolute;
  margin-top: 46px;
  z-index: 2;
}

.topSection img {
  float: left;
}

@media only screen and (max-width: 375px) {
  .topSection .logo img {
    width: 100%;
  }
}

.topSection .search {
  position: relative;
  background-color: #393433;
  width: 100%;
  height: 72px;
  margin-top: 70px;
}

.topSection input {
  float: right;
  box-sizing: border-box;
  background-color: #6c6d6b;
  color: rgb(255, 255, 255);
  margin-top: 22px;
  padding: 7px 5px;
  border: 1px solid #6c6d6b;
  border-radius: 4px 0px 0px 4px;
  width: 20%;
}

.topSection button {
  float: right;
  margin-top: 21px;
  margin-right: 5%;
  border-radius: 0px 5px 5px 0px;
  width: 120px;
  cursor: pointer;
  font: bold 12px/21px;
  padding: 7px 0px;
  text-transform: uppercase;
  color: #fff;
  background: url(data:image/png;base64,) 0 0 repeat-x #930706;
  border-bottom: 1px solid #c40a0a;
  letter-spacing: 3px;
}

@media only screen and (max-width: 1000px) {
  .topSection .logo {
    margin-top: 10px;
  }

  .topSection .search {
    height: 50px;
    margin-top: 60px;
    padding-top: 60px;
  }

  .topSection input {
    margin-top: 0px;
    width: 60%;
  }

  .topSection button {
    margin-top: 0px;
  }
}

.navigation {
  background-color: #1e1e1e;
  width: 100%;
}
.navigation ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-image: linear-gradient(#494848, #141414);
  /*background-color: rgb(39, 38, 38);*/
  border-top: 1px solid rgb(102, 100, 100);
  border-bottom: 1px solid black;
}

.navigation li {
  float: left;
  border-right: 1px solid black;
  text-transform: uppercase;
}

.navigation li a {
  display: block;
  color: white;
  text-align: center;
  padding: 18px 16px;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 2px;
}

.navigation li a:hover:not(.active) {
  background-color: #111;
}

.navigation .active {
  background: url(data:image/png;base64,) center bottom no-repeat;
  border-bottom: 5px solid #a20707;
  background-color: #222123;
  padding: 18px 16px;
}

.mobile {
  display: none;
}

@media only screen and (max-width: 1000px) {
  .navigation {
    display: none;
  }
  .mobile {
    display: block;
  }
}

